<template>
  <el-drawer class="drawerDL" :visible.sync="visibleSync" v-bind="$attrs" v-on="$attrs.rootOn">
    <div class="hd-box" slot="title">
      <div class="hd-left">
        <slot name="title">{{ $attrs.title }}</slot>
      </div>
      <div class="hd-right" v-if="$slots.hr">
        <slot name="hr"></slot>
      </div>
    </div>
    <div class="drawer__content">
      <div class="contentBox">
        <slot></slot>
      </div>
      <div class="footerBox" v-if="$slots.fl || $slots.fr">
        <div class="fl">
          <slot name="fl"></slot>
        </div>
        <div class="fr">
          <slot name="fr"></slot>
        </div>
      </div>
    </div>
  </el-drawer>
</template>

<script lang="ts">
export default {
  name: "wkDrawer",
  // 双向绑定的属性 需手动注册触发sync
  props: {
    visible: {
      type: Boolean,
    },
  },
  computed: {
    visibleSync: {
      get() {
        return this.visible;
      },
      set(value) {
        this.$emit("update:visible", value);
      },
    },
  },
};
</script>

<style lang="less" scoped>
::v-deep {
  .noPadding {
    margin: -20px -30px;
  }
}
.hd-box {
  display: flex;
  justify-content: space-between;
  .hd-left {
    font-size: 14px;
    font-weight: bold;
    line-height: 20px;
    color: #262626;
  }
  .hd-right {
    text-align: right;
    padding-right: 20px;
    ::v-deep {
      .item {
        font-size: 13px;
        margin-left: 24px;
        cursor: pointer;
        color: #262626;
      }
      .icon {
        font-size: 16px;
      }
    }
  }
}
.drawerDL {
  .drawer__content {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  .contentBox {
    overflow: auto;
    flex: 1;
    padding: 20px 30px;
  }
  .footerBox {
    padding: 15px 24px;
    border-top: 1px solid #e5e5e5;
    text-align: right;
    display: flex;
    justify-content: space-between;
  }

  ::v-deep {
    .el-drawer__body {
      height: calc(100% - 58px);
    }
    .el-drawer__header {
      padding: 16px 24px;
      border-bottom: 1px solid #e5e5e5;
      margin-bottom: 0;
    }
  }
}
</style>
