import dialog from './business/dialog/index.js';
import drawer from './business/drawer/index.js';
import frame from './business/frame/index.js';
import Button from './form/button/index.js';
import upload from './form/upload/index.js';

const components = [
  Button,
  dialog,
  frame,
  upload,
  drawer
];

const install = function (Vue, opts = {}) {
  components.forEach(component => {
    Vue.component(component.name, component);
  });
};

/* istanbul ignore if */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  install,
  Button,
  dialog,
  frame,
  upload,
  drawer
};
