<template>
  <el-dialog class="dialogDL" top="5vh" :visible.sync="visibleSync" v-bind="$attrs" v-on="$attrs.rootOn">
    <slot></slot>
    <template slot="footer" v-if="$slots.fl || $slots.fr">
      <div class="fl">
        <slot name="fl"></slot>
      </div>
      <div class="fr">
        <slot name="fr"></slot>
      </div>
    </template>
  </el-dialog>
</template>

<script>
export default {
  name: "wkDialog",
  props: {
    visible: {
      type: Boolean,
    },
  },
  computed: {
    visibleSync: {
      get() {
        return this.visible;
      },
      set(value) {
        this.$emit("update:visible", value);
      },
    },
  },
};
</script>

<style lang="less" scoped>
.dialogDL {
  ::v-deep {
    .el-select {
      width: 100%;
    }
    .el-dialog__footer {
      display: flex;
      text-align: left;
      justify-content: space-between;
    }
    .el-dialog__body {
      padding: 24px;
    }
    .el-dialog__header {
      padding: 16px 24px;
      border-bottom: 1px solid #e5e5e5;
      margin-bottom: 0;
    }
    .el-dialog__title {
      font-size: 14px;
      font-weight: bold;
      line-height: 20px;
    }
  }
}
</style>
