<template>
  <el-dialog
    class="uploadBox"
    v-loading="loading"
    :title="title"
    :visible.sync="visibleSync"
    :close-on-click-modal="false"
    append-to-body
    width="440px"
    @close="clear"
  >
    <div class="download">
      <el-link
        class="btn"
        :underline="false"
        type="primary"
        icon="el-icon-document-copy"
        @click="downloadTemplate"
      >
        下载模板
      </el-link>
      <span class="tip">（请按照模板填写，切勿合并单元格与修改样式）</span>
    </div>
    <el-upload
      ref="upload"
      drag
      :action="importUrl || ''"
      :data="params"
      :before-upload="beforeUpload"
      :on-success="successUpload"
      :on-error="errorUpload"
      :file-list="fileList"
      :on-change="changeUpload"
      :auto-upload="false"
      :accept="`.${fileType.join(',.')}`"
    >
      <el-link
        class="mainTxt"
        :underline="false"
        type="primary"
        icon="el-icon-upload"
        @click="downloadTemplate"
      >
        选择文件 / 拖拽文件至此
      </el-link>
      <div class="limit">
        <span>文件类型：{{ fileType.join("/") }}</span>
        <span v-if="maxSize">，文件大小≤{{ maxSize }}M</span>
      </div>
    </el-upload>
    <template slot="footer">
      <el-button size="small" @click="visibleSync = false">取消</el-button>
      <el-button size="small" type="primary" @click="submitUpload"
        >导 入</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
function initData() {
  return {
    loading: false,
    // 添加的列表
    fileList: [],
  };
}
export default {
  name: "wkUpload",
  components: {},
  props: {
    title: String,
    importUrl: String, // 上传接口
    templateUrl: String, // 下载模板接口
    analysisUrl: String, // 文件分析接口
    params: Object,
    visible: Boolean,
    maxSize: [Number, String], // M为单位
    fileType: {
      type: Array,
      default: function () {
        return ["xls", "xlsx"];
      },
    },
  },
  data() {
    return initData();
  },
  computed: {
    visibleSync: {
      get() {
        return this.visible;
      },
      set(val) {
        this.$emit("update:visible", val);
      },
    },
  },
  methods: {
    // 下载模板
    async downloadTemplate() {
      location.href = this.templateUrl;
    },
    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    changeUpload(file, fileList) {
      // response 则是添加文件
      if (!file.response) {
        this.fileList = [file];
      } else {
        // 上传成功或者失败，都讲fileList置空
        this.fileList = [];
      }
    },
    // 上传前钩子
    beforeUpload(file, fileList) {
      // 拿到后缀
      const FileExt = file.name.replace(/.+\./, "").toLowerCase();
      // 'jpg','png','txt','zip', 'rar','pdf','doc','docx','xlsx'
      if (!this.fileType.includes(FileExt)) {
        this.$message.error("导入文件需为xls、xlsx");
        return false;
      }

      if (this.maxSize) {
        const size = file.size / 1024 / 1024;
        //  超过规格
        if (size > Number(this.maxSize)) {
          this.$message.error(`上传文件大小不超过${this.maxSize}M`);
          return false;
        }
      }
    },
    // 成功钩子
    successUpload(res, file, fileList) {
      this.loading = false;
      if (res.code !== 200) {
        this.$message.error(res.message || "文件上传失败");
        return;
      }
      this.visibleSync = false;
      this.$message.success("上传成功！");
      this.$emit("callback", res);
    },
    // 上传
    submitUpload() {
      this.$refs.upload.submit();
    },
    // 失败钩子
    errorUpload(res, file) {
      this.loading = false;
      this.$message.error("文件上传失败");
    },
    clear() {
      this.fileList = [];
      this.$emit("clear");
    },
  },
};
</script>

<style lang="less" scoped>
.uploadBox {
  ::v-deep {
    .el-dialog {
      border-radius: 8px;
      overflow: hidden;
    }
    .el-dialog__header {
      background: #f5f7fa;
      padding: 0 20px;
      height: 36px;
      line-height: 36px;
      border-bottom: 1px solid #e3e4e4;
      .el-dialog__title {
        font-size: 14px;
        font-weight: bold;
        color: #325065;
      }
      .el-dialog__headerbtn {
        font-size: 20px;
        top: 8px;
      }
    }
    .el-dialog__body {
      padding: 20px;
    }
    .el-upload {
      width: 100%;
      .el-upload-dragger {
        height: 100px;
        width: 100%;
        background: #f8f8f8;
        position: relative;
        display: flex;
        flex-direction: column;
        .mainTxt {
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          .el-icon-upload {
            line-height: inherit;
            margin: 0;
            color: inherit;
            font-size: 20px;
          }
        }
        .limit {
          line-height: 20px;
          color: #bbb;
          background: #ededed;
          padding: 3px 5px;
          font-size: 12px;
        }
      }
    }
  }
}
.download {
  margin-bottom: 10px;
  .btn {
    font-weight: 400;
    cursor: pointer;
  }
}
</style>
